import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { Dialog, DataTable } from '@tencent/bere'

class ConflictDialog extends React.Component {
  handleOK = () => {
    const { refreshAction } = this.props
    this.props.dispatch(refreshAction)
    this.props.dispatch({ type: 'productManage/closeConflictDialog' })
  }

  handleCancel = () => {
    this.props.dispatch({ type: 'productManage/closeConflictDialog' })
  }

  render() {
    const { title, visible, hint, data, keys } = this.props

    const hintEle = hint.split('#').map(value => {
      switch (value) {
      case '{length}':
        return <span className="text-danger">{data.length}</span>
      case '{link}':
        return (
          <Link to="/jobs" title="检测项目管理">
              检测项目管理
          </Link>
        )
      default:
        return value
      }
    })

    return (
      <Dialog
        title={title}
        visible={visible}
        okText="刷新检测"
        cancelText="取消"
        onOk={this.handleOK}
        onCancel={this.handleCancel}
      >
        <React.Fragment>
          <p className="text mb-10">{hintEle}</p>
          {keys[0] ? (
            <DataTable
              columns={keys.map(({ key, title }) => ({
                key,
                dataIndex: key,
                title
              }))}
              data={data}
              scroll={{ y: 330 }}
            />
          ) : (
            ''
          )}
        </React.Fragment>
      </Dialog>
    )
  }
}

export default connect(({ productManage }) => {
  const { conflict } = productManage
  return {
    title: conflict.title,
    visible: conflict.visible,
    hint: conflict.hint,
    refreshAction: conflict.refreshAction,
    data: conflict.data,
    keys: conflict.keys
  }
})(ConflictDialog)
