import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { Dialog, Button, Select, Input, TopTips } from '@tencent/bere'

import { message } from '@constant/dataPath'
import validator from './validator'
import PathEditor from './PathEditor'

function validate(data) {
  for (const field in data) {
    if (!validator[field]) {
      continue
    }

    const error = validator[field](data[field], data)
    if (error) {
      return {
        message: error,
        field,
        value: data[field]
      }
    }
  }
  return null
}

class ItemEditor extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  dispatchChange(obj){
    
    const data = {
      csv_img_path: `${this.state.img_position_1}:${this.state.img_position_2}`,
      csv_defect_size_path: `${this.state.ds_position_1}:${
        this.state.ds_position_2
      }`,
      csv_ratio_path: `${this.state.ratio_position_1}:${
        this.state.ratio_position_2
      }`,
      csv_archieve1_path: `${this.state.archieve1_position_1}:${
        this.state.archieve1_position_2
      }`
    }

    this.props.dispatch({
      type: obj.type,
      payload: {...data,...obj.payload}
    })
  }

  componentWillMount() {
    const id = this.props.match.params.id
    if (id === 'new') {
      this.props.dispatch({ type: 'dataPath/showAdd' })
    } else {
      this.props.dispatch({
        type: 'dataPath/fetchPath',
        payload: { id }
      })
      this.dispatchChange({
        type: 'dataPath/change',
        payload: { id, editMode: 'update' }
      })
    }
  }
  componentWillReceiveProps(nextProps) {
    const props = nextProps
    let img_position_1 = ''
    let img_position_2 = ''
    let ds_position_1 = ''
    let ds_position_2 = ''
    let ratio_position_1 = ''
    let ratio_position_2 = ''
    let archieve1_position_1 = ''
    let archieve1_position_2 = ''
    let arr
    if (props.csv_img_path) {
      arr = props.csv_img_path.split(':')
      if (arr[0]) {
        img_position_1 = arr[0]
      }
      if (arr[1]) {
        img_position_2 = arr[1]
      }
    }

    if (props.csv_defect_size_path) {
      arr = props.csv_defect_size_path.split(':')
      if (arr[0]) {
        ds_position_1 = arr[0]
      }
      if (arr[1]) {
        ds_position_2 = arr[1]
      }
    }

    if (props.csv_ratio_path) {
      arr = props.csv_ratio_path.split(':')
      if (arr[0]) {
        ratio_position_1 = arr[0]
      }
      if (arr[1]) {
        ratio_position_2 = arr[1]
      }
    }
    if (props.csv_archieve1_path) {
      arr = props.csv_archieve1_path.split(':')
      if (arr[0]) {
        archieve1_position_1 = arr[0]
      }
      if (arr[1]) {
        archieve1_position_2 = arr[1]
      }
    }
    this.setState({
      img_position_1,
      img_position_2,
      ds_position_1,
      ds_position_2,
      ratio_position_1,
      ratio_position_2,
      archieve1_position_1,
      archieve1_position_2
    })
  }

  handleSiteChange = site => {
    this.dispatchChange({
      type: 'dataPath/change',
      payload: { site, shop: '', operation: '' }
    })
  }
  handleShopChange = shop => {
    this.dispatchChange({
      type: 'dataPath/change',
      payload: { shop, operation: '' }
    })
  }

  handleChange = (field, e) => {
    this.dispatchChange({
      type: 'dataPath/change',
      payload: { [field]: e.target.value.trim() }
    })
  }
  handleCSVContentChange = (field, e) => {
    this.setState({ [field]: e.target.value.trim() })
  }

  handleEditPath = field => {
    this.dispatchChange({
      type: 'dataPath/change',
      payload: { showPathEditor: true, editing_field: field }
    })
  }
  handleCopyPath = (from, to) => {
    const data = {
    }
    data[to] = this.props[from]

    this.dispatchChange({
      type: `dataPath/change`,
      payload: {...data }
    })
  }
  

  handleOK = () => {
    const {
      id,
      site,
      shop,
      public_path,
      csv_path,
      nas_img_path,
      siteMap,
      editMode,
      isSubmitting
    } = this.props

    if (isSubmitting) {
      return
    }

    const data = {
      id,
      site,
      shop,
      public_path: public_path.replace(/[^\d\.]/g, ''),
      csv_path,
      nas_img_path,
      csv_img_path: `${this.state.img_position_1}:${this.state.img_position_2}`,
      csv_defect_size_path: `${this.state.ds_position_1}:${
        this.state.ds_position_2
      }`,
      csv_ratio_path: `${this.state.ratio_position_1}:${
        this.state.ratio_position_2
      }`,
      csv_archieve1_path: `${this.state.archieve1_position_1}:${
        this.state.archieve1_position_2
      }`,
      siteMap
    }

    if (data.csv_img_path.length == 1) {
      data.csv_img_path = ''
    }
    if (data.csv_defect_size_path.length == 1) {
      data.csv_defect_size_path = ''
    }
    if (data.csv_ratio_path.length == 1) {
      data.csv_ratio_path = ''
    }
    if (data.csv_archieve1_path.length == 1) {
      data.csv_archieve1_path = ''
    }

    const error = validate(data)
    if (error) {
      TopTips({ theme: 'error', message: error.message })
      return
    }

    this.props.dispatch({ type: 'dataPath/startSubmit', payload: { ...data } })

    const aText = editMode == 'add' ? '新增' : '更新'
    const $this = this
    this.props.dispatch({
      type: `dataPath/${editMode}Path`,
      payload: { ...data },
      callback(resp) {
        if (resp && resp.code == 0) {
          TopTips({
            theme: 'success',
            message: aText + '成功'
          })
          $this.props.dispatch({
            type: 'dataPath/endSubmit',
            payload: { ...data }
          })
          $this.props.history.replace('/product/dataPath')
        } else {
          TopTips({
            theme: 'error',
            message: aText + '失败：' + (resp  && resp.msg)
          })
          $this.props.dispatch({
            type: 'dataPath/endSubmit',
            payload: { ...data }
          })
        }
      }
    })
  }

  render() {
    const {
      site,
      shop,
      public_path,
      csv_path,
      nas_img_path,
      siteMap,
      isSiteMapLoading,
      editMode,
      showPathEditor
    } = this.props

    let curSite
    let shopList = []
    const siteList = siteMap.map(item => {
      if (item.site === site) {
        curSite = item
      }
      return { text: item.site, value: item.site }
    })

    if (curSite && curSite.factory_list) {
      shopList = curSite.factory_list.map(item => {
        return { text: item.factory_name, value: item.factory_name }
      })
    }

    const fieldDatas = [
      {
        title: '图片名称',
        field1: { name: 'img_position_1', placeholder: '如DEFECT_DATA_BEGIN' },
        field2: { name: 'img_position_2', placeholder: '如Image_Data' }
      },
      {
        title: 'DEFECT_SIZE',
        field1: { name: 'ds_position_1', placeholder: '如DEFECT_DATA_BEGIN' },
        field2: { name: 'ds_position_2', placeholder: '如DEFECT_SIZE' }
      },
      {
        title: 'RATIO（选填）',
        field1: {
          name: 'ratio_position_1',
          placeholder: '如DEFECT_DATA_BEGIN'
        },
        field2: { name: 'ratio_position_2', placeholder: '如RATIO' }
      },
      {
        title: 'Archieve1（选填）',
        field1: {
          name: 'archieve1_position_1',
          placeholder: '如DEFECT_DATA_BEGIN'
        },
        field2: { name: 'archieve1_position_2', placeholder: '如Archieve1' }
      }
    ]

    return (
      <div>
        <div className="manage-area-title secondary-title">
          <Link to="/product/dataPath" title="返回" className="back-link">
            <i className="btn-back-icon" />
            返回
          </Link>
          <span className="line-icon">|</span>
          <h2>
            {editMode == 'update' ? '修改' : '新建'}
            数据路径
          </h2>
        </div>
        <div className="manage-area-main secondary-main">
          <div className="manage-area-main-inner">
            <div className="tc-panel">
              <div className="tc-panel-bd">
                <div className="param-box">
                  <div className="param-hd">
                    <h3>基础信息</h3>
                  </div>
                  <div className="param-bd">
                    <ul className="form-list">
                      <li>
                        <div className="form-label">
                          <label htmlFor="addPath-site">厂区</label>
                        </div>
                        <Select
                          id="addPath-site"
                          value={site}
                          list={siteList}
                          disabled={isSiteMapLoading}
                          onChange={this.handleSiteChange}
                          placeholder="请选择厂区"
                        />
                      </li>
                      <li>
                        <div className="form-label">
                          <label htmlFor="addPath-shop">厂别</label>
                        </div>
                        <Select
                          id="addPath-shop"
                          value={shop}
                          list={shopList}
                          disabled={isSiteMapLoading}
                          onChange={this.handleShopChange}
                          placeholder="请选择厂别"
                          disabled={!site}
                        />
                      </li>
                      <li>
                        <div className="form-label">
                          <label htmlFor="addPath-public_path">
                            公共路径(IP)
                          </label>
                        </div>
                        <Input
                          id="addPath-public_path"
                          onChange={e => this.handleChange('public_path', e)}
                          value={public_path}
                          placeholder="请输入IP，如192.168.1.1"
                        />
                      </li>
                      <li className="pure-text-row">
                        <div className="form-label ">
                          <label htmlFor="">CSV路径</label>
                        </div>
                        <div className="form-input">
                          <div className="form-unit">
                            <p className="form-text">
                              <span className="mr-10">{csv_path}</span>
                              <a
                                href="javascript:;"
                                onClick={e => this.handleEditPath('csv_path')}
                              >
                                编辑
                              </a>
                              {csv_path ? (
                                <a
                                  href="javascript:;"
                                  onClick={e =>
                                    this.handleCopyPath(
                                      'csv_path',
                                      'nas_img_path'
                                    )
                                  }
                                  style={{ 'paddingLeft': '20px' }}
                                >
                                  复制到NAS图片路径
                                </a>
                              ) : (
                                ''
                              )}
                            </p>
                          </div>
                        </div>
                      </li>
                      <li className="pure-text-row">
                        <div className="form-label ">
                          <label htmlFor="">NAS图片路径</label>
                        </div>
                        <div className="form-input">
                          <div className="form-unit">
                            <p className="form-text">
                              <span className="mr-10">{nas_img_path}</span>
                              <a
                                href="javascript:;"
                                onClick={e =>
                                  this.handleEditPath('nas_img_path')
                                }
                              >
                                编辑
                              </a>
                              {nas_img_path ? (
                                <a
                                  href="javascript:;"
                                  style={{ 'paddingLeft': '20px' }}
                                  onClick={e =>
                                    this.handleCopyPath(
                                      'nas_img_path',
                                      'csv_path'
                                    )
                                  }
                                >
                                  复制到CSV路径
                                </a>
                              ) : (
                                ''
                              )}
                            </p>
                          </div>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
                <div className="param-box">
                  <div className="param-hd">
                    <h3>CSV文件内容解析</h3>
                  </div>
                  <div className="param-bd">
                    <ul className="form-list">
                      {fieldDatas.map((item, index) => {
                        return (
                          <li key={index}>
                            <div className="form-label ">
                              <label htmlFor="">{item.title}</label>
                            </div>
                            <div className="form-input">
                              <div className="form-unit">
                                <div className="tc-input-group-wrap mr-10">
                                  <div className="tc-input-group">
                                    <span className="tc-input-group-addon">
                                      数据段
                                    </span>
                                    <Input
                                      onChange={e =>
                                        this.handleCSVContentChange(
                                          item.field1.name,
                                          e
                                        )
                                      }
                                      value={this.state[item.field1.name]}
                                      placeholder={item.field1.placeholder}
                                    />
                                  </div>
                                </div>

                                <div className="tc-input-group-wrap">
                                  <div className="tc-input-group">
                                    <span className="tc-input-group-addon">
                                      字段
                                    </span>
                                    <Input
                                      onChange={e =>
                                        this.handleCSVContentChange(
                                          item.field2.name,
                                          e
                                        )
                                      }
                                      value={this.state[item.field2.name]}
                                      placeholder={item.field2.placeholder}
                                    />
                                  </div>
                                </div>
                              </div>
                            </div>
                          </li>
                        )
                      })}
                    </ul>
                  </div>
                </div>
              </div>
              <div className="tc-panel-ft">
                <Button onClick={this.handleOK}>确定</Button>
                <Link to="/product/dataPath" title="取消">
                  <Button className="tc-15-btn weak">取消</Button>
                </Link>
              </div>
            </div>
          </div>
        </div>
        {showPathEditor ? <PathEditor /> : ''}
      </div>
    )
  }
}

export default connect(({ dataPath, productManage }) => {
  return {
    editMode: dataPath.editMode,
    id: dataPath.id,
    site: dataPath.site,
    shop: dataPath.shop,
    public_path: dataPath.public_path,
    csv_path: dataPath.csv_path,
    nas_img_path: dataPath.nas_img_path,
    csv_img_path: dataPath.csv_img_path,
    csv_defect_size_path: dataPath.csv_defect_size_path,
    siteMap: productManage.siteMap,
    isSiteMapLoading: productManage.isSiteMapLoading,
    csv_ratio_path: dataPath.csv_ratio_path,
    csv_archieve1_path: dataPath.csv_archieve1_path,
    showPathEditor: dataPath.showPathEditor,
    isSubmitting: dataPath.isSubmitting
  }
})(ItemEditor)
