import React from 'react'
import { connect } from 'react-redux'
import { DataTable } from '@tencent/bere'

class ProductTable extends React.Component {
  handleSort = () => {
    this.props.dispatch({ type: 'product/changeSort' })
  }

  handleSiteFileter = siteFilter => {
    this.props.dispatch({
      type: 'product/filterChange',
      payload: { siteFilter }
    })
  }

  handleShopFileter = shopFilter => {
    this.props.dispatch({
      type: 'product/filterChange',
      payload: { shopFilter }
    })
  }

  handleUpdate = e => {
    const { id } = e.target.parentNode.dataset
    const product = this.props.productList.find(item => item.id == id)
    this.props.dispatch({
      type: 'product/showUpdate',
      payload: { product }
    })
  }

  handleDelete = e => {
    const { id } = e.target.parentNode.dataset

    this.props.dispatch({
      type: `productManage/showConfirmDialog`,
      payload: {
        title: '确定删除所选站点产品？',
        hint: '删除后将无法恢复',
        OKText: '确定',
        confirmAction: {
          type: `product/deleteProduct`,
          payload: { id }
        }
      }
    })
  }

  get columns() {
    const { siteFilter, shopFilter, sites, shops } = this.props
    return [
      {
        key: 'site',
        dataIndex: 'site',
        title: '厂区',
        headerRender: () => (
          <DataTable.FilterableColumnHeader
            list={sites.map(value => ({ text: value, value }))}
            value={siteFilter}
            onFilter={this.handleSiteFileter}
          >
            厂区
          </DataTable.FilterableColumnHeader>
        )
      },
      {
        key: 'factory_name',
        dataIndex: 'factory_name',
        title: '厂别',
        headerRender: () => (
          <DataTable.FilterableColumnHeader
            list={shops.map(value => ({ text: value, value }))}
            value={shopFilter}
            onFilter={this.handleShopFileter}
          >
            厂别
          </DataTable.FilterableColumnHeader>
        )
      },
      {
        key: 'process_operation_name',
        dataIndex: 'process_operation_name',
        title: '站点'
      },
      {
        key: 'product_spec_name',
        dataIndex: 'product_spec_name',
        title: '产品'
      },
      { key: 'ratio', dataIndex: 'ratio', title: '放大倍率' },
      {
        key: 'correlate_detection',
        dataIndex: 'correlate_detection',
        title: '关联检测项目',
        render: (value, record) =>
          value[0] ? (
            <div>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <span className="site-manage-more-list">
                  <a href="javascript:;">{value.length}个</a>
                </span>
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    {value.map(name => (
                      <p key={name}>{name}</p>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div>
              <span>无 </span>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <i className="tc-icon icon-what" />
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    <p>请在检测项目管理页面进行关联</p>
                  </div>
                </div>
              </div>
            </div>
          )
      },
      {
        key: 'modify_time',
        dataIndex: 'modify_time',
        title: '最近更新时间',
        sortable: true
      },
      {
        key: '',
        dataIndex: '',
        title: '操作',
        render: (value, record) => (
          <div key={record.id} data-id={record.id}>
            <a onClick={this.handleUpdate} href="javascript:;">
              修改
            </a>
            <a onClick={this.handleDelete} href="javascript:;">
              删除
            </a>
          </div>
        )
      }
    ]
  }

  render() {
    let { productList } = this.props
    const { isProductLoading, sortOrder, siteFilter, shopFilter } = this.props

    if (siteFilter) {
      productList = productList.filter(item => item.site === siteFilter)
    }

    if (shopFilter) {
      productList = productList.filter(item => item.factory_name === shopFilter)
    }

    return (
      <DataTable
        columns={this.columns}
        loading={isProductLoading}
        data={productList}
        rowKey="id"
        sortKey="modify_time"
        sortOrder={sortOrder}
        onSort={this.handleSort}
      />
    )
  }
}

export default connect(function ({ product, productManage }) {
  return {
    total: product.total,
    limit: product.limit,
    page: product.page,
    isProductLoading: product.isProductLoading,
    productList: product.productList,
    jobsList: product.jobsList,
    sortOrder: product.sortOrder,
    siteMap: productManage.siteMap,
    sites: productManage.sites,
    shops: productManage.shops,
    siteFilter: product.siteFilter,
    shopFilter: product.shopFilter
  }
})(ProductTable)
