import React from 'react'
import { connect } from 'react-redux'
import { Switch, Route, Redirect } from 'react-router-dom'

import Product from '@modules/product'
import Message from '@modules/message'
import dataPath from '@modules/dataPath'

import Tabs from './Tabs'
import ConflictDialog from './ConfilctDialog'
import ConfirmDialog from './ConfirmDialog'

const menu = [
  {
    name: '站点产品管理',
    path: '/product/productList'
  },
  {
    name: '数据路径配置',
    path: '/product/dataPath'
  },
  {
    name: '消息配置',
    path: '/product/message'
  }
]

class App extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'productManage/closeConfirmDialog' })
    this.props.dispatch({ type: 'productManage/closeConflictDialog' })
    this.props.dispatch({ type: 'productManage/fetchSiteMap' })
  }

  render() {
    const { location } = this.props
    return (
      <div className="main adc-manage-main">
        <div className="manage-area">
          <div className="manage-area-title">
            <h2>站点管理</h2>
          </div>
          <div className="manage-area-main">
            <div className="tc-15-tab tc-15-tab-alt tc-special-tab">
              <Tabs location={location} menu={menu} />
              <Switch>
                <Route
                  path="/product"
                  exact
                  render={() => (
                    <Redirect to="/product/productList" title="站点产品管理" />
                  )}
                />
                <Route
                  path="/product/productList"
                  exact
                  async
                  component={Product}
                />
                <Route path="/product/dataPath" async component={dataPath} />
                <Route path="/product/message" async component={Message} />
              </Switch>
            </div>
          </div>
        </div>
        <ConflictDialog />
        <ConfirmDialog />
      </div>
    )
  }
}

export default connect(({ productManage }) => {
  return productManage
})(App)
