import React from 'react'
import { connect } from 'react-redux'
import { DataTable } from '@tencent/bere'
import { Link } from 'react-router-dom'

class MessageTable extends React.Component {
  handleSort = () => {
    this.props.dispatch({ type: 'message/changeSort' })
  }

  handleSiteFileter = siteFilter => {
    this.props.dispatch({
      type: 'message/filterChange',
      payload: { siteFilter }
    })
  }

  handleUpdate = e => {
    const { id } = e.target.parentNode.dataset
    const message = this.props.messageList.find(item => item.id == id)
    this.props.dispatch({
      type: 'message/showUpdate',
      payload: { message }
    })
  }

  handleDelete = e => {
    const { id } = e.target.parentNode.dataset

    this.props.dispatch({
      type: `productManage/showConfirmDialog`,
      payload: {
        title: '确定删除所选消息配置？',
        hint:
          '删除后，回写至华星光电的消息将没有主题信息，且不可恢复，请谨慎操作！' +
          '确定要删除么？',
        OKText: '删除',
        confirmAction: {
          type: `message/deleteMessage`,
          payload: { id }
        }
      }
    })
  }

  get columns() {
    const { siteFilter, sites } = this.props

    return [
      {
        key: 'site',
        dataIndex: 'site',
        title: '厂区',
        headerRender: () => (
          <DataTable.FilterableColumnHeader
            list={sites.map(value => ({ text: value, value }))}
            value={siteFilter}
            onFilter={this.handleSiteFileter}
          >
            厂区
          </DataTable.FilterableColumnHeader>
        )
      },
      {
        key: 'listen',
        dataIndex: 'id',
        title: 'ADC监听消息',
        width: 300,
        render: (value, record) => (
          <div key={record.id}>
            <p>
              <span>Service: {record.listen_service}</span>
            </p>
            <p>
              <span>Network: {record.listen_network}</span>
            </p>
            <p>
              <span>Daemon: {record.listen_daemon}</span>
            </p>
            <p>
              <span>SubjectName: {record.adc_listen}</span>
            </p>
            <p>
              <span>Listener: {record.listener}</span>
            </p>
          </div>
        )
      },
      {
        key: 'response',
        dataIndex: 'id',
        title: 'ADC回复消息',
        width: 300,
        render: (value, record) => (
          <div key={record.id}>
            <p>
              <span>Service: {record.resp_service}</span>
            </p>
            <p>
              <span>Network: {record.resp_network}</span>
            </p>
            <p>
              <span>Daemon: {record.resp_daemon}</span>
            </p>
            <p>
              <span>SubjectName: {record.adc_response}</span>
            </p>
            <p>
              <span>Listener: {record.responser}</span>
            </p>
          </div>
        )
      },
      {
        key: 'modify_time',
        dataIndex: 'modify_time',
        title: '最近更新时间',
        sortable: true
      },
      {
        key: 'operator',
        dataIndex: 'operator',
        title: '操作者'
      },
      {
        key: '',
        dataIndex: '',
        title: '操作',
        render: (value, record) => (
          <div key={record.id} data-id={record.id}>
            <Link to={`/product/message/edit/${record.id}`}>修改</Link>
            <a onClick={this.handleDelete} href="javascript:;">
              删除
            </a>
          </div>
        )
      }
    ]
  }

  render() {
    const { messageList, isMessageLoading, sortOrder } = this.props

    return (
      <DataTable
        columns={this.columns}
        loading={isMessageLoading}
        data={messageList}
        rowKey="id"
        sortKey="modify_time"
        sortOrder={sortOrder}
        onSort={this.handleSort}
      />
    )
  }
}

export default connect(function ({ message, productManage }) {
  return {
    total: message.total,
    limit: message.limit,
    page: message.page,
    isMessageLoading: message.isMessageLoading,
    messageList: message.messageList,
    sortOrder: message.sortOrder,
    sites: productManage.sites,
    siteFilter: message.siteFilter
  }
})(MessageTable)
