import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { Button } from '@tencent/bere'

import PathTable from './PathTable'
import PathPager from './PathPager'

class App extends React.Component {
  componentDidMount = () => {
    this.props.dispatch({ type: 'dataPath/closeEdit' })
  }

  render() {
    return (
      <div className="tab-panel">
        <div className="manage-area-main-inner">
          <div className="tc-action-grid">
            <div className="justify-grid">
              <div className="col">
                  <Link to="/product/dataPath/edit/new" title="新建数据路径">
                    <Button className="m btn-add">新建</Button>
                  </Link>
              </div>
            </div>
          </div>
          <div className="tc-panel panel-table">
            <PathTable />
            <PathPager />
          </div>
        </div>
      </div>
    )
  }
}

export default connect(({ dataPath }) => {
  return {
  }
})(App)
