import React from 'react'
import { connect } from 'react-redux'
import { Dialog, TopTips } from '@tencent/bere'
import SparkMD5 from 'spark-md5'

function getMd5(file) {
  return new Promise((resolve, reject) => {
    const chunkSize = 2097152
    const chunks = Math.ceil(file.size / chunkSize)
    let currentChunk = 0
    const spark = new SparkMD5.ArrayBuffer()
    const fileReader = new FileReader()

    fileReader.onload = function (e) {
      spark.append(e.target.result)
      currentChunk++

      if (currentChunk < chunks) {
        loadNext()
      } else {
        resolve(spark.end())
      }
    }

    fileReader.onerror = function (e) {
      reject(e)
    }

    function loadNext() {
      const start = currentChunk * chunkSize
      const end = start + chunkSize >= file.size ? file.size : start + chunkSize

      fileReader.readAsArrayBuffer(file.slice(start, end))
    }

    loadNext()
  })
}

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onload = function () {
      resolve(reader.result.split(',')[1])
    }
    reader.onerror = function (e) {
      reject(e)
    }
  })
}

class FileUpload extends React.Component {
  state = { fileName: '' }
  md5 = ''
  file = null

  handleFileSelect = e => {
    this.file = e.target.files[0]
    if (this.file.size > 20971520) {
      return TopTips({
        theme: 'error',
        message: '请选择小于20M的 xls 文件'
      })
    }
    this.setState({ fileName: this.file.name })
  }

  handleDownload = e => {
    e.preventDefault()
    this.props.dispatch({ type: 'message/downloadMessage' })
  }

  handleOK = () => {
    const { file } = this
    if (!file) {
      return TopTips({ theme: 'error', message: '请选择文件' })
    }
    Promise.all([getBase64(file), getMd5(file)]).then(([data, md5]) => {
      this.props.dispatch({
        type: 'message/updateMessages',
        payload: { data, md5 }
      })
    })
  }

  handleCancel = () => {
    this.props.dispatch({
      type: 'message/closeFileUpload'
    })
  }

  render() {
    const { fileName } = this.state

    return (
      <Dialog
        title="批量修改消息配置"
        visible={true}
        okText="开始上传"
        cancelText="取消"
        onOk={this.handleOK}
        onCancel={this.handleCancel}
      >
        <div className="tc-15-msg">
          <div className="tip-info">
            批量修改会覆盖原有配置信息，请谨慎操作。
          </div>
        </div>
        <ul className="form-list">
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text">
                第一步：下载已有产品信息模板文件
                <a href="javascript:;" onClick={this.handleDownload}>
                  点击下载文件
                </a>
              </p>
            </div>
          </li>
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text">第二步：编辑文件</p>
            </div>
          </li>
          <li className="pure-text-row">
            <div className="form-input">
              <p className="form-text">第三步：上传文件，覆盖原有信息</p>
            </div>
          </li>
          <li>
            <div className="form-input">
              <div className="form-unit form-upload is-success">
                <input
                  type="text"
                  className="tc-15-input-text m"
                  readOnly
                  placeholder="请选择文件"
                  value={fileName}
                />
                <a
                  href="javascript:void(0);"
                  className="tc-upload-btn"
                  title="请选择文件"
                >
                  <div className="file-input-wrap">
                    <input
                      type="file"
                      title="文件限制提示内容"
                      accept="application/vnd.ms-excel"
                      onChange={this.handleFileSelect}
                    />
                    <span>选择文件</span>
                  </div>
                </a>

                <p className="form-input-help">请选择小于20M的 xls 文件</p>
              </div>
            </div>
          </li>
        </ul>
      </Dialog>
    )
  }
}

export default connect(({ message }) => {
  return {}
})(FileUpload)
