import React from 'react'
import { connect } from 'react-redux'
import { Pager } from '@tencent/bere'

class ProductPager extends React.Component {
  handlePageChange = page => {
    this.props.dispatch({
      type: 'product/change',
      payload: { page }
    })

    this.props.dispatch({ type: 'product/refresh' })
  }

  handlePerPageChange = limit => {
    this.props.dispatch({
      type: 'product/change',
      payload: { limit, page: 0 }
    })

    this.props.dispatch({ type: 'product/refresh' })
  }

  render() {
    const { total, page, limit } = this.props
    return (
      <Pager
        total={total}
        current={page}
        perPageCountList={[15, 30, 45]}
        perPageCount={limit}
        onPageChange={this.handlePageChange}
        onPerPageChange={this.handlePerPageChange}
      />
    )
  }
}

export default connect(({ product }) => {
  return {
    page: product.page,
    limit: product.limit,
    total: product.total,
    searchText: product.searchText
  }
})(ProductPager)
