import React from 'react'
import { connect } from 'react-redux'
import { Dialog, Select, Input, TopTips } from '@tencent/bere'

import { NEW_SIGN, message } from '@constant/product'
import validator from './validator'

function validate(data) {
  for (const field in data) {
    if (!validator[field]) {
      continue
    }

    const error = validator[field](data[field], data)
    if (error) {
      return {
        message: error,
        field,
        value: data[field]
      }
    }
  }
  return null
}

const ascSort = (a, b) => (a > b ? 1 : -1)
const descSort = (a, b) => (b > a ? 1 : -1)

class ProductEditor extends React.Component {
  state = { newSite: '', newShop: '', newOperation: '' }

  handleSiteChange = site => {
    this.props.dispatch({
      type: 'product/change',
      payload: { site, shop: '', operation: '' }
    })
  }

  handleNewSiteChange = e => {
    this.setState({ newSite: e.target.value.replace(' ', '') })
  }

  handleShopChange = shop => {
    this.props.dispatch({
      type: 'product/change',
      payload: { shop, operation: '' }
    })
  }

  handleNewShopChange = e => {
    this.setState({ newShop: e.target.value.replace(' ', '') })
  }

  handleOperationChange = operation => {
    this.props.dispatch({ type: 'product/change', payload: { operation } })
  }

  handleNewOperationChange = e => {
    this.setState({ newOperation: e.target.value.replace(' ', '') })
  }

  handleProductChange = e => {
    this.props.dispatch({
      type: 'product/change',
      payload: { product: e.target.value.replace(' ', '') }
    })
  }

  handleRatioChange = e => {
    this.props.dispatch({
      type: 'product/change',
      payload: { ratio: e.target.value.replace(' ', '') }
    })
  }

  handleCancel = () => {
    this.props.dispatch({ type: 'product/closeEdit' })
  }

  handleOK = () => {
    const {
      id,
      site,
      shop,
      operation,
      product,
      ratio,
      editMode,
      siteMap,
      isSubmitting
    } = this.props
    if (isSubmitting) {
      return
    }

    const { newSite, newShop, newOperation } = this.state
    const data = {
      id,
      site,
      shop,
      operation,
      product,
      ratio,
      newSite,
      newShop,
      newOperation,
      siteMap
    }

    const error = validate(data)
    if (error) {
      TopTips({ theme: 'error', message: error.message })
      return
    }

    this.props.dispatch({ type: 'product/startSubmit' })
    this.props.dispatch({
      type: `product/${editMode}Product`,
      payload: { ...data }
    })
  }

  render() {
    const { newSite, newShop, newOperation } = this.state

    const {
      id,
      site,
      shop,
      operation,
      product,
      ratio,
      siteMap,
      editMode,
      isSiteMapLoading
    } = this.props

    let curSite
    let shopList = []
    let operationList = []

    let siteList = siteMap
      .map(item => {
        if (item.site === site) {
          curSite = item
        }
        return { text: item.site, value: item.site }
      })
      .sort((a, b) => ascSort(a.text, b.text))
    if (curSite && curSite.factory_list) {
      let curShop
      shopList = curSite.factory_list
        .map(item => {
          if (item.factory_name === shop) {
            curShop = item
          }
          return { text: item.factory_name, value: item.factory_name }
        })
        .sort((a, b) => ascSort(a.text, b.text))

      if (curShop && curShop.pon_list) {
        operationList = curShop.pon_list
          .map(item => ({
            text: item.process_operation_name,
            value: item.process_operation_name
          }))
          .sort((a, b) => ascSort(a.text, b.text))
      }
    }

    siteList = [{ text: '新建厂区', value: NEW_SIGN }, ...siteList]
    shopList = [{ text: '新建厂别', value: NEW_SIGN }, ...shopList]
    operationList = [{ text: '新建站点', value: NEW_SIGN }, ...operationList]

    return (
      <Dialog
        title={message[editMode].title}
        okText="确定"
        visible={true}
        cancelText="取消"
        onOk={this.handleOK}
        onCancel={this.handleCancel}
      >
        {editMode == 'add' ? (
          <div className="tc-15-msg">
            <div className="tip-info">{message[editMode].intro}</div>
          </div>
        ) : (
          ''
        )}

        <ul className="form-list">
          <li>
            <div className="form-label">
              <label htmlFor="addProduct-site">厂区</label>
            </div>
            <Select
              id="addProduct-site"
              value={site}
              list={siteList}
              onChange={this.handleSiteChange}
              placeholder="请选择厂区"
              disabled={editMode === 'update' || isSiteMapLoading}
            />
            {site === NEW_SIGN ? (
              <Input
                className="ml-10"
                placeholder="请输入厂区，如t1"
                onChange={this.handleNewSiteChange}
                value={newSite}
              />
            ) : (
              ''
            )}
          </li>
          <li>
            <div className="form-label">
              <label htmlFor="addProduct-shop">厂别</label>
            </div>
            <Select
              id="addProduct-shop"
              value={shop}
              list={shopList}
              onChange={this.handleShopChange}
              placeholder="请选择厂别"
              disabled={editMode === 'update' || !site}
            />
            {shop === NEW_SIGN ? (
              <Input
                className="ml-10"
                placeholder="如ARRAY"
                onChange={this.handleNewShopChange}
                value={newShop}
              />
            ) : (
              ''
            )}
          </li>
          <li>
            <div className="form-label">
              <label htmlFor="addProduct-operation">站点</label>
            </div>
            <Select
              id="addProduct-operation"
              value={operation}
              list={operationList}
              onChange={this.handleOperationChange}
              placeholder="请选择站点"
              disabled={editMode === 'update' || !shop}
            />
            {operation === NEW_SIGN ? (
              <Input
                className="ml-10"
                placeholder="如3350"
                onChange={this.handleNewOperationChange}
                value={newOperation}
              />
            ) : (
              ''
            )}
          </li>
          <li>
            <div className="form-label">
              <label htmlFor="addProduct-product">产品</label>
            </div>
            <Input
              id="addProduct-product"
              placeholder="如TA4851CAB000"
              onChange={this.handleProductChange}
              value={product}
            />
          </li>
          <li>
            <div className="form-label">
              <label htmlFor="addProduct-ratio">放大倍率</label>
            </div>
            <Input
              id="addProduct-ratio"
              placeholder="请输入倍率"
              onChange={this.handleRatioChange}
              value={ratio}
            />
          </li>
        </ul>
      </Dialog>
    )
  }
}

export default connect(({ product, productManage }) => {
  return {
    editMode: product.editMode,
    id: product.id,
    site: product.site,
    shop: product.shop,
    operation: product.operation,
    product: product.product,
    ratio: product.ratio,
    siteMap: productManage.siteMap,
    isSiteMapLoading: productManage.isSiteMapLoading,
    isSubmitting: product.isSubmitting
  }
})(ProductEditor)
