import React from 'react'
import { connect } from 'react-redux'
import { Select, Input, TopTips, Button } from '@tencent/bere'
import { Link } from 'react-router-dom'

import { message } from '@constant/message'
import validator from './validator'

function validate(data) {
  for (const field in data) {
    if (!validator[field]) {
      continue
    }

    const error = validator[field](data[field], data)
    if (error) {
      return {
        message: error,
        field,
        value: data[field]
      }
    }
  }
  return null
}

const formInputs = [
  {
    key: 'listen_service',
    name: 'service监听参数',
    placeholder: '如8200'
  },
  {
    key: 'listen_network',
    name: 'network监听参数',
    placeholder: '如225.8.2.0'
  },
  {
    key: 'listen_daemon',
    name: 'daemon监听参数',
    placeholder: '如172.16.3.9:7500'
  },
  {
    key: 'adc_listen',
    name: 'SubjectName监听参数',
    placeholder: '如CSOT.G85B.ADC.ABC'
  },
  {
    key: 'listener',
    name: 'Listener监听参数',
    placeholder: '如CSOTListener'
  },
  {
    key: 'resp_service',
    name: 'service回复参数',
    placeholder: '如8200'
  },
  {
    key: 'resp_network',
    name: 'network回复参数',
    placeholder: '如225.8.2.0'
  },
  {
    key: 'resp_daemon',
    name: 'daemon回复参数',
    placeholder: '如172.16.3.9:7500'
  },
  {
    key: 'adc_response',
    name: 'SubjectName回复参数',
    placeholder: '如CSOT.G85B.ADC.CBA'
  },
  {
    key: 'responser',
    name: 'Listener回复参数',
    placeholder: '如PEMListener'
  }
]

class MessageEditor extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'message/fetchSiteMap' })

    const id = this.props.match.params.id
    if (id === 'new') {
      this.props.dispatch({ type: 'message/initializeNewMessage' })
    } else {
      this.props.dispatch({
        type: 'message/fetchMessage',
        payload: { id }
      })
      this.props.dispatch({
        type: 'message/change',
        payload: { id, editMode: 'update' }
      })
    }
  }

  handleSiteChange = site => {
    this.props.dispatch({
      type: 'message/change',
      payload: { site }
    })
  }

  handleInputChange = e => {
    const key = e.target.name
    const value = e.target.value.replace(' ', '')
    this.props.dispatch({
      type: 'message/change',
      payload: { [key]: value }
    })
  }

  handleCancel = () => {
    this.props.dispatch({ type: 'message/closeEdit' })
  }

  handleOK = () => {
    const {
      id,
      site,
      adc_listen,
      adc_response,
      listener,
      responser,
      listen_service,
      resp_service,
      listen_network,
      resp_network,
      listen_daemon,
      resp_daemon,
      editMode,
      isSubmitting
    } = this.props

    if (isSubmitting) {
      return
    }

    const data = {
      id,
      site,
      listen_service,
      listen_network,
      listen_daemon,
      listener,
      resp_service,
      resp_network,
      resp_daemon,
      adc_listen,
      responser,
      adc_response
    }

    const error = validate(data)
    if (error) {
      TopTips({ theme: 'error', message: error.message })
      return
    }

    this.props.dispatch({ type: 'message/startSubmit' })
    this.props.dispatch({
      type: `message/${editMode}Message`,
      payload: { ...data }
    })
  }

  render() {
    const id = this.props.match.params.id
    const editMode = id === 'new' ? 'add' : 'update'
    const { site, siteMap } = this.props

    const siteList = siteMap.map(item => ({
      text: item.site,
      value: item.site
    }))

    return (
      <div className="manage-area">
        <div className="manage-area-title secondary-title">
          <Link to="/product/message" className="back-link" title="消息配置">
            <i className="btn-back-icon" />
            返回
          </Link>
          <span className="line-icon">|</span>
          <h2>{editMode === 'add' ? '新建消息配置' : '编辑消息配置'}</h2>
        </div>
        <div className="manage-area-main secondary-main">
          <div className="manage-area-main-inner">
            <div className="tc-panel">
              <div className="tc-panel-bd">
                <div className="param-box">
                  <ul className="form-list">
                    <li>
                      <div className="form-label">
                        <label htmlFor="addMessage-site">厂区</label>
                      </div>
                      <Select
                        id="addMessage-site"
                        value={site}
                        list={siteList}
                        onChange={this.handleSiteChange}
                        placeholder="请选择厂区"
                      />
                    </li>
                    {formInputs.map(({ key, name, placeholder }) => (
                      <li key={key}>
                        <div className="form-label">
                          <label htmlFor={`addMessage-${key}`}>{name}</label>
                        </div>
                        <Input
                          id={`addMessage-${key}`}
                          onChange={this.handleInputChange}
                          name={key}
                          placeholder={placeholder}
                          value={this.props[key]}
                        />
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
              <div className="tc-panel-ft">
                <Button onClick={this.handleOK}>确定</Button>
                <Link to="/product/message" title="消息配置管理">
                  <Button className="tc-15-btn weak">取消</Button>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default connect(({ message }) => {
  return {
    editMode: message.editMode,
    site: message.site,
    siteMap: message.siteMap,
    id: message.id,
    adc_listen: message.adc_listen,
    adc_response: message.adc_response,
    listener: message.listener,
    responser: message.responser,
    listen_service: message.listen_service,
    resp_service: message.resp_service,
    listen_network: message.listen_network,
    resp_network: message.resp_network,
    listen_daemon: message.listen_daemon,
    resp_daemon: message.resp_daemon,
    isSubmitting: message.isSubmitting
  }
})(MessageEditor)
