import React from 'react'
import { Link } from 'react-router-dom'
import classNames from 'classnames'

export default class Tabs extends React.Component {
  getSelectedMenuKeys() {
    let {
      location: { pathname }
    } = this.props
    const pathArr = pathname.split('/')
    if (pathArr.length > 3) {
      pathname = pathArr.slice(0, 3).join('/')
    }
    return pathname
  }

  render() {
    const selectedKeys = this.getSelectedMenuKeys()
    const { menu } = this.props

    return (
      <ul role="tablist" className="tc-15-tablist">
        {menu.map(item => (
          <li
            key={item.path}
            className={classNames({
              'tc-cur': item.path.indexOf(selectedKeys) > -1
            })}
          >
            <Link to={item.path} title={item.name} role="tab">
              {item.name}
            </Link>
          </li>
        ))}
      </ul>
    )
  }
}
