import React from 'react'
import { connect } from 'react-redux'
import { Button } from '@tencent/bere'
import { Link } from 'react-router-dom'

import MessageTable from './MessageTable'
import MessageSearch from './MessageSearch'
import FileUpload from './FileUpload'
import MessagePager from './MessagePager'

class MessageList extends React.Component {
  componentDidMount = () => {
    this.props.dispatch({ type: 'message/closeFileUpload' })
    this.props.dispatch({ type: 'message/closeEdit' })
    this.props.dispatch({ type: 'message/refresh' })

    this.props.history.listen(() => {
      this.props.dispatch({ type: 'message/initialize' })
    })
  }

  handleUpdateMany = () => {
    this.props.dispatch({ type: 'message/showFileUpload' })
  }

  render() {
    const { fileUpload } = this.props

    return (
      <div className="tab-panel">
        <div className="manage-area-main-inner">
          <div className="tc-action-grid">
            <div className="justify-grid">
              <div className="col">
                <Link to="/product/message/edit/new">
                  <Button className="m btn-add" onClick={this.handleAddMessage}>
                    新建
                  </Button>
                </Link>
                <Button className="m weak" onClick={this.handleUpdateMany}>
                  批量修改
                </Button>
              </div>
              <div className="col">
                <MessageSearch />
              </div>
            </div>
          </div>
          <div className="tc-panel panel-table">
            <MessageTable />
            <MessagePager />
          </div>
        </div>
        {fileUpload ? <FileUpload /> : ''}
      </div>
    )
  }
}

export default connect(({ message }) => {
  return {
    deleteDialog: message.deleteDialog,
    fileUpload: message.fileUpload
  }
})(MessageList)
