import React from 'react'
import { connect } from 'react-redux'
import { Switch, Route } from 'react-router-dom'

import PathList from './PathList'
import ItemEditor from './ItemEditor'

class App extends React.Component {
  componentDidMount = () => {
    this.props.dispatch({ type: 'dataPath/closeEdit' })
  }

  render() {
    const { editMode } = this.props
    return (
      <div className="tab-panel">
        <Switch>
          <Route path="/product/dataPath/edit/:id" excat component={ItemEditor} />
          <Route path="/product/dataPath" component={PathList} />
        </Switch>
      </div>
    )
  }
}

export default connect(({ dataPath }) => {
  return {
    editMode: dataPath.editMode
  }
})(App)
