import { NEW_SIGN } from '@constant/product'
const alphaReg = /^[a-zA-Z]+$/
const numReg = /^[0-9]+$/
const alphaNumReg = /^[a-zA-Z0-9]+$/

export default {
  site(value, data) {
    if (!value) {
      return '请填写厂区'
    }
    if (value === NEW_SIGN) {
      if (!data.newSite) {
        return '请填写新建厂区名称'
      }

      if (!alphaNumReg.test(data.newSite)) {
        return '厂区名称仅支持数字、字母'
      }

      if (data.newSite.length > 2) {
        return '厂区名称不超过2个字'
      }

      for (const s of data.siteMap) {
        if (s.site == data.newSite) {
          return '新建厂区名称重复，请重新填写'
        }
      }
    }
  },
  shop(value, data) {
    if (!value) {
      return '请填写厂别'
    }

    if (value === NEW_SIGN) {
      if (!data.newShop) {
        return '请填写新建厂别名称'
      }

      if (!alphaReg.test(data.newShop)) {
        return '厂别名称仅支持字母'
      }

      if (data.newShop.length > 5 || data.newShop.length < 2) {
        return '厂别名称为2到5个字'
      }

      // 厂别，同一厂区的厂别不能重名
      if (data.site != NEW_SIGN) {
        for (const s of data.siteMap) {
          if (s.site == data.site && s.factory_list) {
            for (const f of s.factory_list) {
              if (f.factory_name == data.newShop) {
                return '新建厂别名称重复，请重新填写'
              }
            }
          }
        }
      }
    }
  },
  operation(value, data) {
    if (!value) {
      return '请填写站点'
    }
    if (value === NEW_SIGN) {
      if (!data.newOperation) {
        return '请填写新建站点名称'
      }

      if (!numReg.test(data.newOperation)) {
        return '站点名称仅支持数字'
      }

      if (data.newOperation.length > 10) {
        return '站点名称不超过10个字'
      }

      // 站点，同一厂别的站点不能重名
      if (data.shop != NEW_SIGN) {
        for (const s of data.siteMap) {
          if (s.site == data.site && s.factory_list) {
            for (const f of s.factory_list) {
              if (f.factory_name == data.shop && f.pon_list) {
                for (const o of f.pon_list) {
                  if (
                    o.process_operation_name ==
                    data.newOperation
                  ) {
                    return '新建站点名称重复，请重新填写'
                  }
                }
              }
            }
          }
        }
      }
    }
  },
  product(value) {
    if (!value) {
      return '请填写产品名称'
    }

    if (!alphaNumReg.test(value)) {
      return '产品名称仅支持字母和数字'
    }

    if (value.length > 12) {
      return '产品名称不超过12个字'
    }
  },
  ratio(value) {
    if (!value && value !== 0) {
      return '请填写放大倍率'
    }

    if (!numReg.test(value) || value < 1 || value > 99) {
      return '放大倍率必须为1-99之间的整数'
    }
  }
}
