import React from 'react'
import { connect } from 'react-redux'
import { Button } from '@tencent/bere'

import ProductTable from './ProductTable'
import ProductSearch from './ProductSearch'
import ProductEditor from './ProductEditor'
import FileUpload from './FileUpload'
import ProductPager from './ProductPager'

class App extends React.Component {
  componentDidMount = () => {
    this.props.dispatch({ type: 'product/closeFileUpload' })
    this.props.dispatch({ type: 'product/closeEdit' })
    this.props.dispatch({ type: 'product/refresh' })

    this.props.history.listen(() => {
      this.props.dispatch({ type: 'product/initialize' })
    })
  }

  handleAddProduct = () => {
    this.props.dispatch({ type: 'product/showAdd' })
  }

  handleUpdateMany = () => {
    this.props.dispatch({ type: 'product/showFileUpload' })
  }

  render() {
    const { editMode, fileUpload } = this.props

    return (
      <div className="tab-panel">
        <div className="manage-area-main-inner">
          <div className="tc-action-grid">
            <div className="justify-grid">
              <div className="col">
                <Button className="m btn-add" onClick={this.handleAddProduct}>
                  新建
                </Button>
                <Button className="m weak" onClick={this.handleUpdateMany}>
                  批量修改
                </Button>
              </div>
              <div className="col">
                <ProductSearch />
              </div>
            </div>
          </div>
          <div className="tc-panel panel-table">
            <ProductTable />
            <ProductPager />
          </div>
        </div>
        {editMode ? <ProductEditor /> : ''}
        {fileUpload ? <FileUpload /> : ''}
      </div>
    )
  }
}

export default connect(({ product }) => {
  return {
    editMode: product.editMode,
    deleteDialog: product.deleteDialog,
    fileUpload: product.fileUpload
  }
})(App)
