import React from 'react'
import { connect } from 'react-redux'
import { Dialog, Select, Input, TopTips } from '@tencent/bere'

import { message } from '@constant/dataPath'
import validator from './validator'

function validate(data) {
  for (const field in data) {
    if (!validator[field]) {
      continue
    }

    const error = validator[field](data[field], data)
    if (error) {
      return {
        message: error,
        field,
        value: data[field]
      }
    }
  }
  return null
}

class PathEditor extends React.Component {
    constructor(props){
        super(props);
        const {editing_field} = props;
        const org_path = props[editing_field];
        let sPaths;
        // sPaths=[
        //     {
        //         type:1,//type 1，输入，2选择，3选择并限制前N位，
        //         sels:{
        //             value1:"",
        //             value2:""
        //         },
        //         limit:"",
        //         text:""
        //     },
        //     {
        //         type:3,//type 1，输入，2选择，3选择并限制前N位，
        //         sels:{
        //             value1:"",
        //             value2:""
        //         },
        //         limit:"",
        //         text:""
        //     },
        //     {
        //         type:2,//type 1，输入，2选择，3选择并限制前N位，
        //         sels:{
        //             value1:"",
        //             value2:""
        //         },
        //         limit:"",
        //         text:""
        //     }
        // ]
        sPaths = this.getPaths(org_path);
        this.state = {sPaths:sPaths,disPath:org_path};
    }

  componentDidMount() {
  }
  componentDidUpdate(){
  }
  getPaths = (p)=>{
      let ps = p.split("/");
      let sPath=[];
      const reg = /(\{)?([^\}\{\[]+)(\[0\-)?(\d*)(\])?(\})?/
      for(let str of ps){
          let m = reg.exec(str);
          if(m){
              let obj={
                  type:1,//type 1，输入，2选择，3选择并限制前N位，
                  sels:{
                      value1:"",
                      value2:""
                  },
                  limit:"",
                  text:""
              }
              let type=1;
              if(m[1]&&m[6]){//type=2或3
                obj.sels.value1=m[2];
                if(m[3]&&m[5]){
                    obj.type=3;
                    obj.limit=m[4];
                    obj.sels.value2="2";
                }
                else{
                    obj.type=2;
                    obj.sels.value2="1";
                }
              }
              else{
                obj.text = m[2];
              }
              sPath.push(obj);
          }
      }
      return sPath;
  }
  getDisPath=(sPaths)=>{
    let disPath = "";
    for(let p of sPaths){
        if(p.type==1){
            
            let n_value = p.text;
            n_value = n_value.replace(/^([\/]+)/,"");
            n_value = n_value.replace(/([\/]+)$/,"");
            n_value = n_value.replace(/([\/]+)/g,"/");;
            disPath +="/"+n_value;
        }
        else{
            if(p.type==2){
                disPath +="/{"+p.sels.value1+"}";
            }
            else{
                disPath +="/{"+p.sels.value1+"[0-"+p.limit+"]}";
            }
        }
    }
    return disPath;
  }

  setPathsState=(sPaths)=>{
    let disPath = this.getDisPath(sPaths);
    this.setState({sPath:sPaths,disPath:disPath})
  }


  handleChange = (index,field,e) => {
    let sPaths = this.state.sPaths;

    sPaths[index][field] = e.target.value;

    this.setPathsState(sPaths)
    // this.props.dispatch({
    //   type: 'dataPath/change',
    //   payload: { [field]: e.target.value }
    // })
  }
  handleSelChange = (index,field,v) => {

    let sPaths = this.state.sPaths;

    sPaths[index]["sels"][field] = v;

    if(field =="value2"){
        if(v=="2"){//输入长度限制
            sPaths[index]["type"]=3;
        }
        else{//取消长度限制
            sPaths[index]["type"]=2;
        }
    }
    this.setPathsState(sPaths)
  }
  handleDown = (index) => {
    let sPaths = this.state.sPaths;
    if(index <(sPaths.length-1)){
          let path = sPaths[index];
          let path_n = sPaths[index+1];
          sPaths.splice(index,2,path_n,path);
    }
    this.setPathsState(sPaths)
  }
  handleUp = index => {
    const sPaths = this.state.sPaths
    if (index > 0) {
      const path = sPaths[index]
      const path_n = sPaths[index - 1]
      sPaths.splice(index - 1, 2, path, path_n)
      }
      this.setPathsState(sPaths)
  }
  handleDel = (index) => {
    let sPaths = this.state.sPaths;
    sPaths.splice(index,1);
    this.setPathsState(sPaths)
  }
  handleAddField =(type)=>{
    let sPaths = this.state.sPaths;
    let path = {
        type:type,//type 1，输入，2选择，3选择并限制前N位，
        sels:{
            value1:"",
            value2:""
        },
        limit:"",
        text:""
    };
    sPaths.push(path);
    this.setPathsState(sPaths)
  }
  
  

  handleCancel = () => {
    this.props.dispatch({
      type: 'dataPath/change',
      payload: { showPathEditor:false}
    })
  }

  handleOK = () => {
    const {
      editing_field
    } = this.props

    const data = {
    }
    data[editing_field] = this.state.disPath;

    const error = validate({"editing_field":this.state.disPath})
    if (error) {
      TopTips({ theme: 'error', message: error.message })
      return
    }

    this.props.dispatch({
      type: `dataPath/change`,
      payload: {showPathEditor:false, ...data }
    })
  }

  render() {
    const {
      public_path
    } = this.props

    let selList1 = [{
        text:"HEADER:MESSAGENAME",
        value:"HEADER:MESSAGENAME"
    },{
        text:"HEADER:TRANSACTIONID",
        value:"HEADER:TRANSACTIONID"
    },{
        text:"HEADER:REPLYSUBJECTNAME",
        value:"HEADER:REPLYSUBJECTNAME"
    },{
        text:"HEADER:LISTENER",
        value:"HEADER:LISTENER"
    },{
        text:"BODY:PRODUCTNAME",
        value:"BODY:PRODUCTNAME"
    },{
        text:"BODY:PRODUCTSPECNAME",
        value:"BODY:PRODUCTSPECNAME"
    },{
        text:"BODY:EQPTYPE",
        value:"BODY:EQPTYPE"
    },{
        text:"BODY:PROCESSOPERATIONNAME",
        value:"BODY:PROCESSOPERATIONNAME"
    },{
        text:"BODY:SITE",
        value:"BODY:SITE"
    },{
        text:"BODY:FACTORYNAME",
        value:"BODY:FACTORYNAME"
    }]
    let selList2 = [{
        text:"全部字段",
        value:"1"
    },{
        text:"前N位",
        value:"2"
    }]

    return (
      <Dialog
        title="编辑路径"
        okText="确定"
        visible={true}
        cancelText="取消"
        onOk={this.handleOK}
        onCancel={this.handleCancel}
        style={{width:"800px"}}
      >
        <ul className="form-list form-list-vertical">
                    <li>
                        <div className="form-label">
                            <label htmlFor="">预览</label>
                        </div>
                        <div className="form-input">
                            <div className="form-unit">
                                <div className="tc-15-msg adc-dialog-msg-gray">
                                    <div className="tip-info">{this.state.disPath}</div>
                                </div>
                            </div>
                        </div>
                    </li>
                    <li>
                        <div className="form-label">
                            <label htmlFor="">编辑</label>
                        </div>
                        <div className="form-input">
                            <div className="form-unit">
                                <div className="tc-15-table-panel">
                                    <div className="tc-15-table-fixed-head">
                                        <table className="tc-15-table-box">
                                            <colgroup>
                                                <col style={{"width":"60px"}} />
                                                <col />
                                                <col style={{"width":"130px"}} />
                                            </colgroup>
                                            <thead>
                                                <tr>
                                                    <th>
                                                        <div>
                                                            <span className="text-overflow">顺序</span>
                                                        </div>
                                                        <i className="resize-line-icon"></i>
                                                    </th>
                                                    <th>
                                                        <div>
                                                            <span className="text-overflow">内容</span>
                                                        </div>
                                                        <i className="resize-line-icon"></i>
                                                    </th>
                                                    <th>
                                                        <div>
                                                            <span className="text-overflow">操作</span>
                                                        </div>
                                                    </th>
                                                </tr>
                                            </thead>
                                        </table>
                                    </div>
                                    <div className="tc-15-table-fixed-body"  style={{"maxHeight": (screen.availHeight/3)+"px","overflow": "scroll","display": "block"}}>
                                        <table className="tc-15-table-box tc-15-table-rowhover">
                                            <colgroup>
                                                <col style={{"width":"60px"}} />
                                                <col />
                                                <col style={{"width":"130px"}} />
                                            </colgroup>
                                            <tbody>
                                                {this.state.sPaths.map((sPath,index,sPaths) =>{
                                                    return (
                                                    <tr key={index.toString()}>
                                                        <td>
                                                            <div>
                                                                <span>{index+1}</span>
                                                            </div>
                                                        </td>
                                                        <td>
                                                            <div>
                                                                {(sPath.type==2||sPath.type==3)?(  
                                                                    <Select
                                                                    className="tc-15-select m mr-5"
                                                                    value={sPath.sels.value1}
                                                                    list={selList1}
                                                                    selectFirstByDefault={false}
                                                                    placeholder="请选择字段"
                                                                    onChange={v =>this.handleSelChange(index,"value1",v)}
                                                                    />
                                                                ):""}
                                                                
                                                                {(sPath.type==2||sPath.type==3)?(  
                                                                    <Select
                                                                    disabled ={!sPath.sels.value1}
                                                                    className="tc-15-select m"
                                                                    value={sPath.sels.value2}
                                                                    list={selList2}
                                                                    selectFirstByDefault={true}
                                                                    onChange={v =>this.handleSelChange(index,"value2",v)}
                                                                    />
                                                                ):""}
                                                                
                                                                {sPath.type==3?(  
                                                                    <div className="tc-input-group-wrap s adc-dialog-group-wrap">
                                                                        <div className="tc-input-group">
                                                                            <span className="tc-input-group-addon">N=</span>
                                                                            <Input  style={{"min-width":"83px"}} className="tc-15-input-text s" value={sPath.limit} type="number" min="1" max="1000"
                                                                            onChange={e =>this.handleChange(index,"limit",e)} />
                                                                        </div>
                                                                    </div>
                                                                ):""}
                                                                {sPath.type==1?(  
                                                                    <Input  className="tc-15-input-text xl" value={sPath.text} 
                                                                    onChange={e =>this.handleChange(index,"text",e)} />
                                                                ):""}
                                                            </div>
                                                        </td>
                                                        <td>
                                                            <div>
                                                                <a href="javascript:;" onClick={e =>this.handleDel(index)}>删除</a>
                                                                {
                                                                  index == sPaths.length - 1 ? '' : 
                                                                  <a href="javascript:;" onClick={e =>this.handleDown(index)}>下移</a>
                                                                }
                                                                {
                                                                  index == 0 ? '' : 
                                                                  <a href="javascript:;" onClick={e =>this.handleUp(index)}>上移</a>
                                                                }
                                                            </div>
                                                        </td>
                                                    </tr>)
                                                    }
                                                )}
                                            </tbody>
                                        </table>
                                    </div>
                                    <div className="tc-15-table-fixed-body">
                                        <table className="tc-15-table-box tc-15-table-rowhover">
                                            <colgroup>
                                                <col style={{"width":"60px"}} />
                                                <col />
                                                <col style={{"width":"130px"}} />
                                            </colgroup>
                                            <tbody>
                                            <tr>
                                                <td colSpan="3">
                                                    <div className="text-left">
                                                        <a href="javascript:;" onClick={e =>this.handleAddField(2)}>+ 增加逻辑生成字段</a>
                                                        <a href="javascript:;" onClick={e =>this.handleAddField(1)}>+ 增加输入字段</a>
                                                    </div>
                                                </td>
                                            </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </li>
                </ul>
      </Dialog>
    )
  }
}

export default connect(({ dataPath }) => {
  return {
    public_path: dataPath.public_path,
    csv_path: dataPath.csv_path,
    nas_img_path: dataPath.nas_img_path,
    editing_field: dataPath.editing_field
  }
})(PathEditor)
