export default {
  site(value) {
    if (!value) {
      return '请填写厂区'
    }
  },
  adc_listen(value) {
    if (!value) {
      return '请填写SubjectName监听参数'
    }

    if (value.length > 50) {
      return 'SubjectName监听参数长度不超过50个字'
    }
  },
  listener(value) {
    if (!value) {
      return '请填写Listener监听参数'
    }

    if (value.length > 50) {
      return 'Listener监听参数长度不超过50个字'
    }
  },
  listen_service(value) {
    if (!value) {
      return '请填写service监听参数'
    }

    if (value.length > 30) {
      return 'service监听参数长度不超过30个字'
    }
  },
  listen_network(value) {
    if (!value) {
      return '请填写network监听参数'
    }

    if (value.length > 15 || value.length < 7) {
      return 'network监听参数长度应在7到15个字之间'
    }
  },
  listen_daemon(value) {
    if (!value) {
      return '请填写daemon监听参数'
    }

    if (value.length > 30) {
      return 'daemon监听参数长度不超过30个字'
    }
  },
  adc_response(value) {
    if (!value) {
      return '请填写SubjectName回复参数'
    }

    if (value.length > 50) {
      return 'SubjectName回复参数长度不超过50个字'
    }
  },
  responser(value) {
    if (!value) {
      return '请填写Listener回复参数'
    }

    if (value.length > 50) {
      return 'Listener回复参数长度不超过50个字'
    }
  },
  resp_service(value) {
    if (!value) {
      return '请填写service回复参数'
    }

    if (value.length > 30) {
      return 'service回复参数长度不超过30个字'
    }
  },
  resp_network(value) {
    if (!value) {
      return '请填写network回复参数'
    }

    if (value.length > 15 || value.length < 7) {
      return 'network回复参数长度应在7到15个字之间'
    }
  },
  resp_daemon(value) {
    if (!value) {
      return '请填写daemon回复参数'
    }

    if (value.length > 30) {
      return 'daemon回复参数长度不超过30个字'
    }
  }
}
