import React from 'react'
import { connect } from 'react-redux'
import { Pager } from '@tencent/bere'

class MessagePager extends React.Component {
  handlePageChange = page => {
    this.props.dispatch({
      type: 'message/change',
      payload: { page }
    })

    this.props.dispatch({ type: 'message/refresh' })
  }

  handlePerPageChange = limit => {
    this.props.dispatch({
      type: 'message/change',
      payload: { limit, page: 0 }
    })

    this.props.dispatch({ type: 'message/refresh' })
  }

  render() {
    const { total, page, limit } = this.props
    return (
      <Pager
        total={total}
        current={page}
        perPageCountList={[15, 30, 45]}
        perPageCount={limit}
        onPageChange={this.handlePageChange}
        onPerPageChange={this.handlePerPageChange}
      />
    )
  }
}

export default connect(({ message }) => {
  return {
    page: message.page,
    limit: message.limit,
    total: message.total,
    searchText: message.searchText
  }
})(MessagePager)
