import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { DataTable, TopTips } from '@tencent/bere'

const ascSort = (a, b) => a > b
const descSort = (a, b) => b > a

class PathTable extends React.Component {
  state = { siteFilter: null, shopFilter: null, sortOrder: null }

  componentDidMount() {
    this.refresh()
  }

  refresh = () => {
    const { page, limit, searchText } = this.props

    this.props.dispatch({
      type: 'dataPath/fetchPaths',
      payload: { page, limit, searchText }
    })
  }

  handleSort = () => {
    this.props.dispatch({ type: 'dataPath/changeSort' })
  }

  handleSiteFileter = siteFilter => {
    this.props.dispatch({
      type: 'dataPath/filterChange',
      payload: { siteFilter }
    })
  }

  handleShopFileter = shopFilter => {
    this.props.dispatch({
      type: 'dataPath/filterChange',
      payload: { shopFilter }
    })
  }

  handleUpdate = e => {
    const { id } = e.target.parentNode.dataset
    const dataPath = this.props.pathList.find(item => item.id == id)
    this.props.dispatch({
      type: 'dataPath/showUpdate',
      payload: { dataPath }
    })
  }

  handleDelete = e => {
    const { id } = e.target.parentNode.dataset
    const $this = this
    this.props.dispatch({
      type: `productManage/showConfirmDialog`,
      payload: {
        title: '确定删除所选数据路径？',
        hint: '删除后将无法恢复',
        OKText: '确定',
        confirmAction: {
          type: `dataPath/deletePath`,
          payload: { id },
          callback: function (resp) {
            if (resp  && resp.code == 0) {
              TopTips({
                theme: 'success',
                message: '删除成功'
              })
              $this.refresh()
            } else {
              TopTips({
                theme: 'error',
                message: '删除失败：' + (resp &&  resp.msg)
              })
            }
          }
        }
      }
    })
  }

  get columns() {
    const { pathList, sites, shops, siteFilter, shopFilter } = this.props

    return [
      {
        key: 'site',
        dataIndex: 'site',
        title: '厂区',
        headerRender: () => (
          <DataTable.FilterableColumnHeader
            list={sites.map(value => ({ text: value, value }))}
            value={siteFilter}
            onFilter={this.handleSiteFileter}
          >
            厂区
          </DataTable.FilterableColumnHeader>
        )
      },
      {
        key: 'factory_name',
        dataIndex: 'factory_name',
        title: '厂别',
        headerRender: () => (
          <DataTable.FilterableColumnHeader
            list={shops.map(value => ({ text: value, value }))}
            value={shopFilter}
            onFilter={this.handleShopFileter}
          >
            厂别
          </DataTable.FilterableColumnHeader>
        )
      },
      {
        key: 'public_path',
        dataIndex: 'public_path',
        title: '公共路径'
      },
      {
        key: 'csv_path',
        dataIndex: 'csv_path',
        title: 'CSV路径'
      },
      {
        key: 'nas_img_path',
        dataIndex: 'nas_img_path',
        title: 'NAS图片路径'
      },
      {
        key: 'csv_img_path',
        dataIndex: 'csv_img_path',
        title: 'CSV图像位置'
      },
      {
        key: 'correlate_detection',
        dataIndex: 'correlate_detection',
        title: '关联检测项目',
        render: (value, record) =>
          value ? (
            <div>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <span className="site-manage-more-list">
                  <a href="javascript:;">{value.length}个</a>
                </span>
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    {value.map(name => (
                      <p key={name}>{name}</p>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div>
              <span>无 </span>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <i className="tc-icon icon-what" />
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    <p>请在检测项目管理页面进行关联</p>
                  </div>
                </div>
              </div>
            </div>
          )
      },
      {
        key: 'operator',
        dataIndex: 'operator',
        title: '操作者'
      },
      {
        key: '',
        dataIndex: '',
        title: '操作',
        render: (value, record) => (
          <div key={record.id} data-id={record.id}>
            <Link
              to={`/product/dataPath/edit/${record.id}`}
              title="数据路径修改"
            >
              修改
            </Link>
            <a onClick={this.handleDelete} href="javascript:;">
              删除
            </a>
          </div>
        )
      }
    ]
  }

  render() {
    let { pathList } = this.props
    const { siteFilter, shopFilter, sortOrder } = this.state
    const { isDataPathLoading } = this.props

    if (siteFilter) {
      pathList = pathList.filter(item => item.site === siteFilter)
    }

    if (shopFilter) {
      pathList = pathList.filter(item => item.factory_name === shopFilter)
    }

    if (sortOrder) {
      const newList = [...pathList]
      newList.sort((a, b) =>
        (sortOrder === 'desc' ? descSort : ascSort)(
          a.modify_time,
          b.modify_time
        )
      )
      pathList = newList
    }

    return (
      <DataTable
        columns={this.columns}
        loading={isDataPathLoading}
        data={pathList}
        rowKey="id"
        sortKey="modify_time"
        sortOrder={sortOrder}
        onSort={this.handleSort}
      />
    )
  }
}

export default connect(function ({ dataPath, productManage }) {
  return {
    total: dataPath.total,
    limit: dataPath.limit,
    page: dataPath.page,
    isDataPathLoading: dataPath.isDataPathLoading,
    pathList: dataPath.pathList,
    jobsList: dataPath.jobsList,
    sortOrder: dataPath.sortOrder,
    sites: productManage.sites,
    shops: productManage.shops
  }
})(PathTable)
