import React from 'react'
import { connect } from 'react-redux'
import { Dialog } from '@tencent/bere'

class ConfirmDialog extends React.Component {
  handleOK = () => {
    const { confirmAction } = this.props

    this.props.dispatch({ type: 'productManage/closeConfirmDialog' })
    this.props.dispatch(confirmAction)
  }

  handleCancel = () => {
    this.props.dispatch({ type: 'productManage/closeConfirmDialog' })
  }

  render() {
    const { title, visible, okText, hint } = this.props
    return (
      <Dialog
        title={title}
        visible={visible}
        okText={okText}
        cancelText="取消"
        onOk={this.handleOK}
        onCancel={this.handleCancel}
      >
        {hint}
      </Dialog>
    )
  }
}

export default connect(({ productManage }) => {
  const { confirm } = productManage
  return {
    confirmAction: confirm.confirmAction,
    title: confirm.title,
    visible: confirm.visible,
    okText: confirm.okText,
    hint: confirm.hint
  }
})(ConfirmDialog)
